@charset "utf-8";
@import "_smart-grid";

.section4 {
	//border: 1px solid rgb(255, 104, 16);
	margin-top: -100px;
	height: 100%;
	width: 100%;

	@include col();
	@include size-xs(12);

	@include xs(margin-top, 35px);

	&__logo {
		margin: 0 auto;
	}

	&__title {
		padding-top: -30px;
		font-family: 'Roboto';
		font-size: 47px;
		font-weight: 900;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.02;
		letter-spacing: normal;
		text-align: center;
		color: #000000;

		@include xs(font-size, 35px);

		span {
			color: #fa4f01;
			@include xs(font-size, 35px);
		}

		p {
			//border: 1px solid rgb(255, 104, 16);
			margin-top: -25px;
			opacity: .9;
			font-family: 'Roboto';
			font-size: 23px;
			font-weight: normal;
			line-height: 2.09;
			text-align: center;
			color: #444444;

			@include xs(font-size, 17px);
		}
	}

	&__box {
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		.box1 {
			//border: 1px solid rgb(255, 104, 16);
			width: 30%;
			height: 350px;

			@include col();
			@include size-xs(12);


			.logo2 {

				margin-left: 98px;
				margin-top: -32px;

				@include xs(margin-left, 80px);
				@include xs(position, absolute);

			}

			.ram1 {
				width: 100%;

			}

			.ram2 {
				margin-top: -7px;
				@include xs(margin-top, 40px);
			}

			.logo3 {

				margin-left: 130px;
				padding-bottom: 9px;
				@include xs(margin-left, 111px);

			}

			&__logo1 {
				//border: solid 1px #f64d01;
				margin-left: 116px;
				margin-bottom: -30px;

				@include xs(margin-left, 98px);
			}

			&__h3 {
				//border: solid 1px #f64d01;
				margin-top: -230px;
				font-family: 'Roboto';
				font-size: 24px;
				font-weight: bold;
				line-height: 1.17;
				text-align: center;
				color: #000000;

				@include xs(margin-top, -200px);
				@include xs(font-size, 20px);
			}

			&__p {
				margin: 0 25px;
				font-family: 'Roboto';
				font-size: 17px;
				font-weight: normal;
				margin-top: -10px;
				line-height: 1.44;
				letter-spacing: normal;
				text-align: center;
				color: #444444;

				@include xs(font-size, 16px);
				@include xs(line-height, 1.2);

				.box1__span {
					font-weight: 500;
					color: #000000;
				}
			}
		}

	}

	&__footer {
		//border: solid 1px #f64d01;
		margin: 32px auto;
		@include xs(width, 100%);

		.logo3 {
			//border: solid 1px #f64d01;
			@include xs(width, 100%);
		}

		p {
			margin-top: -70px;
			font-family: 'Roboto';
			font-size: 27px;
			font-weight: 500;
			font-style: normal;
			font-stretch: normal;
			line-height: 0.98;
			letter-spacing: normal;
			text-align: center;
			color: #000000;

			@include xs(margin-top, -42px);
			@include xs(font-size, 16px);
			@include xs(opacity, .8);

		}

		span {
			color: #f64d01;
			@include xs(font-size, 16px);
		}
	}
}
