@charset "utf-8";
@import "_smart-grid";
@import "settings/_settings";
@import "fonts/_fonts";



header {
	//height: 887px;
	//height: 100%;
	//width: 100%;
	background: url(../img/slo1.png);
	background-size: cover;

	@include xs(background, none);


	.mico {
		display: flex;
		position: absolute;
		top: 0;
		right: 0;
		left: 380px;
		z-index: 3;


		@include xs(left, 0);
		@include xs(margin, 20px auto);
		@include xs(position, relative);

		img {
			width: 81px;
			height: 56px;

		}

		p {
			padding-left: 10px;
			font-family: "Roboto";
			font-size: 22px;
			font-weight: 900;
			font-style: normal;
			font-stretch: normal;
			line-height: 1.82;
			letter-spacing: normal;
			text-align: left;
			color: #383334;
		}
	}

	.header__box {
		//border: 1px solid rgb(16, 255, 76);
		display: flex;
		flex-wrap: wrap;
		position: relative;
		min-height: 650px;


		@include col();
		@include size-xs(12);



		.headImg {
			//border: 1px solid rgb(16, 255, 76);
			position: absolute;
			top: 0;
			left: -330px;
			width: 900px;

			//@include xs(visibility, hidden);
			@include xs(display, none);
			//@include xs(top, -9999);

			img {
				//@include xs(display, none);
				//@include xs(visibility, hidden);
				width: 100%;
				height: 100%;

			}
		}


		.Strela {
			//border: 1px solid rgb(16, 255, 76);
			position: absolute;
			margin-right: -22px;
			right: 0;
			top: 42%;
			z-index: 1;
			width: 49px;
			height: 225px;

			@include xs(right, 27px);
			@include xs(top, 10%);

		}

		.boxFormaHeader {
			//border: 1px solid rgb(255, 104, 16);
			margin-top: 24px;
			display: flex;
			flex-wrap: wrap;


			@include xs(flex-direction, column);
			@include xs(display, none);

			.headerBox {
				display: flex;

				//border: 1px solid rgb(16, 255, 88);
				width: 213px;
				height: 41px;

				@include xs(margin, 10px auto);
				@include xs(width, 100%);


				&__img1 {
					width: 39px;
					height: 39px;

				}

				&__img2 {
					padding-left: 30px;
					width: 26px;
					height: 41px;
					@include xs(padding-left, 0);
				}

				&__img3 {
					height: 35px;

				}

				p {
					//border: 1px solid rgb(126, 139, 130);
					margin-left: 10px;
					margin: auto 0;
					height: 35px;
					font-family: 'Roboto';
					font-size: 14px;
					font-weight: normal;
					font-style: italic;
					font-stretch: normal;
					line-height: 1.13;
					letter-spacing: normal;
					text-align: left;
					color: #666666;


					@include xs(font-size, 18px);

				}
			}

		}

		.boxforma {
			//border: 1px solid rgb(255, 104, 16);
			height: 87%;
			width: 280px;
			position: absolute;
			right: 0;
			bottom: 0;
			background: url(../img/Ramka.png) no-repeat;

			@include xs(height, 565px);
			@include xs(position, relative);
			@include xs(margin, -450px auto);


			img {
				margin-bottom: -5px;

			}



			.boxTitle {
				//border: 1px solid rgb(255, 104, 16);
				display: flex;
				justify-content: center;
				margin: 0 auto;
				width: 120px;


				.HeadText {
					margin-left: 9px;
					margin-right: 2px;
					font-family: 'Roboto';
					font-size: 16px;
					font-weight: normal;
					font-style: normal;
					font-stretch: normal;
					letter-spacing: normal;
					text-align: left;
					color: #444444;
				}


				img {
					//border: 1px solid rgb(255, 104, 16);
					padding: 3px;
					width: 27px;
					height: 15px;
				}
			}

			.Us {
				//border: 1px solid rgb(255, 104, 16);
				margin-top: 35px;

				p {
					width: 100%;
					padding: 0 45px;
					font-family: 'Roboto';
					font-size: 20px;
					font-weight: normal;
					font-style: normal;
					font-stretch: normal;
					line-height: 1.1;
					letter-spacing: normal;
					text-align: center;
					color: #000000;

					span {
						font-weight: 500;
						color: #ff6b00;
					}
				}

			}

			.zena {
				//border: 1px solid rgb(255, 104, 16);
				display: flex;
				flex-wrap: wrap;
				margin-top: -15px;

				.star {
					//	border: 1px solid rgb(255, 104, 16);
					margin-left: 35px;
					font-family: 'Roboto';
					font-size: 14px;
					font-weight: 500;
					line-height: 1.71;
					opacity: .9;
					color: #666666;

				}

				.now {
					//border: 1px solid rgb(255, 104, 16);
					margin-left: 36px;
					font-family: 'Roboto';
					font-size: 14px;
					font-weight: 500;
					line-height: 1.71;
					opacity: .9;
					color: #000000;
				}

				img {
					//border: 1px solid rgb(255, 104, 16);
					margin-top: -10px;
					margin: 0 auto;
				}
			}

			.form {
				//border: 1px solid rgb(255, 104, 16);
				width: 220px;
				margin: 0 auto;
				margin-top: 15px;

				.label-input {
					//border: 1px solid rgb(255, 104, 16);
					display: flex;
					flex-wrap: wrap;

					.W {
						margin-top: 10px;
					}

					.button-submit {
						//border: 1px solid rgb(255, 104, 16);
						position: relative;
						margin-top: 10px;
						display: flex;
						align-items: center;
						align-content: center;
						width: 220px;
						height: 65px;
						border-radius: 32px;
						background-image: linear-gradient(to right, #ffbc02, #ffce02);
						cursor: pointer;

						&:focus {
							outline: none;
							border: none;

						}

						&:hover {
							background-image: linear-gradient(to right, #26c93c, #ffce02);

						}

						&:before {
							content: '';
							position: absolute;
							left: 5px;
							bottom: 5px;
							right: 5px;
							top: 5px;
							opacity: 0.3;
							border-radius: 32px;
							border: solid 3px #ffffff;


						}

						p {
							//border: 1px solid rgb(255, 104, 16);
							font-family: 'Roboto';
							font-size: 16px;
							font-weight: bold;
							font-style: normal;
							font-stretch: normal;
							line-height: 1;
							letter-spacing: normal;
							text-align: center;
							color: #000000;
							margin: 0 auto;

							span {

								font-size: 28px;
								line-height: 0.79;
							}
						}

					}

					label {
						//border: 1px solid rgb(255, 104, 16);
						display: flex;
						align-items: center;
						width: 220px;
						height: 65px;
						border-radius: 32px;
						box-shadow: inset 0px 2px 7px 0 rgba(0, 0, 0, 0.04);
						background-color: #eef2f7;

						.labelBox {
							//border: 1px solid rgb(16, 255, 116);
							padding-left: 15px;
							width: 100%;

							input {
								//border: 1px solid rgb(255, 104, 16);
								outline: none;
								border: none;
								margin: 0 auto;
								width: 150px;
								height: 30px;
								font-family: 'Roboto';
								font-size: 16px;
								font-weight: normal;
								font-style: normal;
								font-stretch: normal;
								line-height: 1.13;
								letter-spacing: normal;
								text-align: left;
								color: #666666;
								padding-left: 10px;
								background-color: #eef2f7;
							}
						}
					}
				}
			}

			.upakov {
				//border: 1px solid rgb(255, 104, 16);
				margin: 0 auto;
				margin-top: 25px;
				width: 195px;
				height: 35px;

				p {
					margin: 0 auto;
					font-family: 'Roboto';
					font-size: 18px;
					font-weight: normal;
					letter-spacing: normal;
					line-height: 1;
					text-align: center;
					color: #333333;
					opacity: .9;

					span {
						font-weight: 500;
						color: #9dbd39;
					}
				}
			}


			.footerbox {
				//border: 1px solid rgb(255, 104, 16);
				position: absolute;
				bottom: 7px;
				left: 0;
				right: 2px;
				display: flex;
				align-content: center;
				align-items: center;
				width: 270px;
				height: 44px;
				margin: 0 auto;
				border-radius: 0 0 15px 15px;
				background-color: #eef2f7;

				@include xs(bottom, 7px);
				@include xs(right, 2px);

				&__one {
					//border: 1px solid rgb(255, 104, 16);
					display: flex;
					align-content: center;
					align-items: center;
					width: 230px;
					height: 24px;
					margin: 0 auto;

					img {
						//border: 1px solid rgb(255, 104, 16);
						margin: 0;
						text-align: center;
					}

					p {
						//border: 1px solid rgb(255, 104, 16);
						margin-left: 12px;
						font-family: 'Roboto';
						font-size: 14px;
						font-weight: normal;
						opacity: .9;
						line-height: 1.18;
						letter-spacing: normal;
						color: #666666;
					}
				}
			}
		}

		.boxIconHeader {
			//border: 1px solid rgb(126, 139, 130);
			position: absolute;
			height: 563px;
			width: 350px;
			bottom: 0;
			left: 0;

			@include xs(position, relative);
			@include xs(margin, 0 auto);
			@include xs(margin-top, -700px);

			&__h3 {
				//border: 1px solid rgb(126, 139, 130);
				margin-top: 0;
				height: 166px;
				font-family: 'Roboto';
				font-size: 44px;
				font-weight: 900;
				font-style: normal;
				font-stretch: normal;
				line-height: 1;
				letter-spacing: normal;
				text-align: left;
				color: #383334;

				@include xs(text-align, center);
				@include xs(margin-top, 20px);
				@include xs(font-size, 40px);

				.span1 {
					position: relative;
					font-size: 34px;
					font-weight: bold;
					line-height: 1.12;
					z-index: 0;

					@include xs(font-size, 30px);

					&:before {
						content: '';
						position: absolute;
						bottom: 0;
						left: 0;
						right: 0;
						border-bottom: 5px solid #ff6b00;
						z-index: -1;
					}

				}

				.span2 {
					font-size: 34px;
					font-weight: bold;

					@include xs(font-size, 30px);

				}

				.span3 {
					position: relative;
					font-size: 34px;
					font-weight: bold;
					line-height: 1.12;

					@include xs(font-size, 30px);

					&:before {
						content: '';
						position: absolute;
						bottom: 0;
						left: 0;
						right: 0;
						border-bottom: 5px solid #a7c93d;
						z-index: -1;
					}
				}
			}

			&__group {
				//border: 1px solid rgb(126, 139, 130);

				.Shape {

					img {
						//border: 1px solid rgb(218, 39, 241);
						position: absolute;
						margin-top: -118px;
						margin-left: 27px;
					}
				}

				.group1 {
					//border: 1px solid rgb(42, 199, 89);
					display: flex;
					align-items: center;
					@include xs(margin-top, -20px);

					p {
						//border: 1px solid rgb(42, 199, 89);
						font-family: 'Roboto';
						font-size: 18px;
						font-weight: normal;
						font-style: normal;
						font-stretch: normal;
						line-height: 1.2;
						letter-spacing: normal;
						text-align: left;
						color: #444444;
						margin-left: 10px;

						.group-span {
							font-weight: 500;
							color: #000000;
						}

						.T {
							position: relative;

							&:before {
								content: '';
								position: absolute;
								bottom: 0;
								left: 0;
								right: 0;
								border-bottom: 3px solid #ff6b00;

							}
						}

						.G {
							position: relative;

							&:before {
								content: '';
								position: absolute;
								bottom: 0;
								left: 0;
								right: 0;
								border-bottom: 3px solid #a7c93d;

							}
						}
					}
				}
			}
		}
	}
}
