@charset "utf-8";
@import "_smart-grid";

.section5 {
	//border: 1px solid rgb(16, 147, 255);
	background: url(../img/sloi5.png) no-repeat;
	background-size: cover;
	margin-top: 50px;

	@include col();
	@include size-xs(12);

	&__logo {
		//border: 1px solid rgb(16, 147, 255);
		padding-top: 30px;
		margin: 0 auto;
	}

	&__title {
		padding-top: -30px;
		width: 100%;

		h3 {
			//border: 1px solid rgb(16, 147, 255);
			font-family: 'Roboto';
			font-size: 47px;
			font-weight: 900;
			line-height: 1.02;
			text-align: center;
			color: #000000;

			@include xs(font-size, 40px);

			span {
				color: #fa4f01;
			}
		}
	}

	&__box {
		//border: 1px solid rgb(16, 147, 255);
		display: flex;
		justify-content: space-between;
		margin: 0 auto;

		.boxFooter {
			//border: 1px solid rgb(16, 147, 255);
			width: 100%;
			text-align: center;
			margin-top: auto;

			img {

				width: 98%;
				margin-bottom: 0;

			}
		}

		.stakan {
			@include xs(display, none);

		}


		.boxFooter1 {
			//border: 1px solid rgb(16, 147, 255);
			height: 400px;
			display: flex;

			&__iconBox {
				//border: 1px solid rgb(16, 147, 255);
				display: flex;
				align-items: center;
				width: 100%;
				height: 135px;

				img {
					//border: 1px solid rgb(16, 147, 255);
					width: 110px;
					height: 110px;

					@include xs(width, 90px);
				}

				p {
					//border: 1px solid rgb(16, 147, 255);
					position: relative;
					margin-left: 28px;
					font-family: 'Roboto';
					font-size: 18px;
					font-weight: 500;
					font-style: normal;
					font-stretch: normal;
					line-height: 1.44;
					letter-spacing: normal;
					color: #000000;

					@include xs(font-size, 15);
					@include xs(text-align, left);

					&:before {
						content: '';
						position: absolute;
						top: -19px;
						left: 0;
						width: 20px;
						border-top: solid 3px #7bbf1c;

					}
				}
			}
		}
	}
}
