@charset "utf-8";
@import "_smart-grid";

.section {
	//border: 1px solid rgb(253, 3, 24);
	margin-top: 250px;
}

.section__logo {
	text-align: center;

}

.section__h2 {
	//border: 1px solid rgb(253, 3, 24);
	margin: 0 auto;

	@include xs(margin-top, -400px);

	.grib {
		//border: 1px solid rgb(255, 111, 16);
		text-align: center;
		height: 47px;
		font-family: 'Roboto';
		font-size: 48px;
		font-weight: 900;
		font-style: normal;
		font-stretch: normal;
		line-height: 1;
		letter-spacing: normal;
		color: #383334;

		@include xs(font-size, 38px);

	}

	.gribok {
		//border: 1px solid rgb(255, 111, 16);
		margin-top: 36px;
		height: 25px;
		font-family: 'Roboto';
		font-size: 24px;
		font-weight: bold;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.17;
		letter-spacing: normal;
		text-align: center;
		color: #2b2828;

		@include xs(font-size, 18px);

	}

}

.section__box {
	display: flex;
	width: 1170px;
	flex-wrap: wrap;
	justify-content: space-between;

	margin: 0 auto;

	@include xs(margin-top, -100px);

	.box {
		//border: 1px solid rgb(255, 111, 16);
		height: 360px;
		width: 314px;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		@include col();
		@include size(3);
		@include size-xs(12);

		width: 30%;

		img {
			//border: 1px solid rgb(255, 111, 16);
			width: 50%;
			height: 46%;
		}

		.text-box {
			//border: 1px solid rgb(16, 147, 255);
			width: 127px;
			height: 73px;
			font-family: 'Roboto';
			font-size: 20px;
			font-weight: 500;
			font-style: normal;
			line-height: 1.18;
			color: #000000;
			text-align: left;
			margin-top: 50px;

		}

		.box-foot {
			position: relative;
			width: 291px;
			height: 73px;
			font-family: 'Roboto';
			font-size: 18px;
			font-weight: 400;
			line-height: 1.56;
			color: #444444;
			padding-top: 20px;
			opacity: .8;
			margin-top: -20px;

			&:after {
				content: '';
				position: absolute;
				width: 50px;
				border-top: solid 3px #becbd8;
				top: 0;
				left: 0;
			}
		}
	}
}

.Ram {
	margin-top: 58px;
	text-align: center;
	width: 100%;

	@include xs(display, none);

	img {
		//border: 1px solid rgb(16, 147, 255);
		width: 100%;

		@include xs(display, none);
	}

	p {
		margin: -90px;
		font-family: 'Roboto';
		font-size: 22px;
		font-weight: normal;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.27;
		letter-spacing: normal;
		color: #444444;

		@include xs(display, none);

		.Statistic {
			font-weight: 500;
			color: #000000;
		}
	}
}



