@charset "utf-8";
@import "_smart-grid";


.footer__box {
		margin-top: 500px;
		margin-bottom: 100px;


	.footer__sety {
		width: 341px;
		display: flex;
		justify-content: space-between;

		&:hover {
			color: #00b9fa;

		}

	}

	.footer__rigth {
		display: flex;
		color: aliceblue;

		a {
			text-decoration: none;
		}

		li {
			display: inline;
			color: #C4C4C4;
			font-family: 'Rubik';
			font-size: 18px;
			font-weight: 300;
			padding-left: 50px;
		}

		.post {
			padding-right: 70px;
			cursor: pointer;

		}

		.tel {
			padding-right: 70px;
		}

		.rus {
			color: #C86DFF;
		}
	}

}
